import login from '../../components/login.vue'
import autoUpdater from '../../utils/autoUpdater.min.js'
export default {
    onShow(){
		if(!uni.getStorageSync('token')) {
			this.islogin = 1
			return false
        }
        this.islogin = 0
        this.getreadprofile()
    },
    onPullDownRefresh(){
        if(!uni.getStorageSync('token')) {
            this.islogin = 1
            uni.stopPullDownRefresh()
			return false
        }
        this.islogin = 0
        this.getreadprofile()
    },
    data(){
        return {
            info:{},
            islogin:0,
            version: '',
            res: {},
            isNew: false
        }
    },

    onLoad () {

























    },
    methods: {
        getreadprofile(){
            this.$http.getreadprofile().then(res=>{
                if(res.status == 200) {
                    uni.setStorageSync('userInfo', JSON.stringify(res.data));
                    this.info = res.data
                }
            })
        },
        confirm(){
            this.$http.getpanduan().then(res=>{
                if(res.status == 200) {
                    if(res.data.zhuangtai == 2) { // 进入审核流程状态页
                        this.getnav('/pages/moneyCartSon/Settledin')
                        
                    } else if(res.data.zhuangtai == 4) {
                        this.getnav('/pages/loginSon/settingWx')
                    }
                    else {
                        this.getnav('/pages/personalSon/TradingType')
                    }
                }
            })
            
        },
        

        checkUpdate () {
			const res = uni.getSystemInfoSync()
			const apptype = res.platform
            if (this.isNew) {
                autoUpdater.init({
                    packageUrl: this.res.url,
                    content: this.res.note,
                    contentAlign: 'left',
                    cancel: '取消',
                    cancelColor: '#007fff'
                })
                autoUpdater.show() 
                } else {
                this.$toast('当前已是最新版本')
            }
        }
 
    },
    components:{
		login
	},
  
    onNavigationBarButtonTap(params) {
        this.getnav('/pages/personalSon/setinfo')
    }
}
